.section.testimonial {
    background: $gold;
}
.section.testimonial .container {
    display: block;
    *zoom: 1;
    padding: 30px 15px;
    padding-bottom: 0;
}
.section.testimonial .container:before {
    content: '';
    display: table;
}
.section.testimonial .container:after {
    content: '';
    display: table;
    clear: both;
}
@media (min-width: $(desktop)) {
    .section.testimonial .container {
        padding: 50px 0 0;
    }
}
.section.testimonial .text {
    float: left;
    font-style: italic;
    color: $white;
    font-family: 'Open Sans';
    font-weight: 600;
    font-size: 26px;
    line-height: 32px;
}
@media (min-width: $(desktop)) {
    .section.testimonial .text {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 6/8 - (30px - 30px * 6/8));
        font-size: 28px;
    }
    .section.testimonial .text:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .section.testimonial .text:last-child {
        margin-right: 0;
    }
    .section.testimonial .text:nth-child(8 '' ''n) {
        margin-right: 0;
        margin-left: auto;
    }
}
.section.testimonial .image {
    position: relative;
    float: right;
    width: 100%;
}
@media (min-width: $(tablet)) {
    .section.testimonial .image {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 1/4 - (30px - 30px * 1/4));
    }
    .section.testimonial .image:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .section.testimonial .image:last-child {
        margin-right: 0;
    }
    .section.testimonial .image:nth-child(4 '' ''n) {
        margin-right: 0;
        margin-left: auto;
    }
}
.section.testimonial .image img {
    float: right;
    mix-blend-mode: multiply;
    -webkit-filter: brightness(1.1);
            filter: brightness(1.1);
    width: 100%;
}
.section.testimonial .image .color-overlay {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: $gold;
    mix-blend-mode: color;
    -webkit-transition: opacity 0.25s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    transition: opacity 0.25s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}
.section.testimonial .name {
    display: block;
    margin-top: 50px;
    color: #424217;
    font-weight: 600;
    font-size: 15px;
    text-transform: uppercase;
}
